import tuiRate from "../../components/rate/rate";
import noData from '../../components/lists/nullData'
import uniLoadMore from '@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
export default {
  components:{
    tuiRate,
    noData,
    uniLoadMore
  },
  data() {
    return {
      rateLists:[],
      current:0,
      imgs:[],
      tabs:[
        {name:'全部',key:''},
        {name:'好评',key:'good'},
        {name:'中评',key:'neutral'},
        {name:'差评',key:'bad'}
      ],
      active:0,
      result:'',
      currentPage:1,
      id:'',
      rateLoading:true,
      pullUpOn:true,
      loadding: false,
      loadStatus: 'loading',
      lastPage:''
    };
  },
  onLoad:function(options){
    this.id = options.id
    this.getRate(true)
  },
  onReachBottom() {
    if (!this.pullUpOn) return;
    this.loadding = true;
    this.loadStatus = 'loading';
    if (this.listPage === this.currentPage) {
      this.loadding = false;
      this.pullUpOn = false;
      this.loadStatus = 'noMore';
    } else {
      this.currentPage++;
      setTimeout(() => {
        this.getRate();
        this.loadding = false;
        this.loadStatus = 'more';
      }, 500);
    }
  },
  methods: {
    showImgs(index,imgs){
      uni.previewImage({
        urls:imgs,
        current:index
      });
    },
    getRate(init){
      if(init){
        this.currentPage = 1;
        this.rateLists = [];
        this.rateLoading = true
      }
      this.$http({
        aurl:'getGoodsRate',
        aPath:[this.id],
        params:{
          result:this.result
        },
        success:res => {
          res.data.result.data.forEach((item)=>{
            this.rateLists.push(item)
          })
          if(init){
            this.lastPage = res.data.result.last_page;
            this.rateLoading = false
          }
          if(res.data.result.last_page===1){
            this.loadding = false;
            this.pullUpOn = false;
            this.loadStatus = 'noMore';
          }
        },
        fail:error => {
          console.log(error)
        }
      })
    },
    activeChange(index){
      this.active = index
      this.result = this.tabs[index].key
      this.getRate(true)
    }
  },
  mounted() {
  },
};
